<template>
    <div id="index">
        <!-- tabs -->
        <div class="index-tabs">
            <div class="tabs-item" v-for="(item, index) in tabsOption" :key="index" :class="[activeIndex === index? 'active': '']" @click="activeIndex = index">{{item}}</div>
        </div>
        <!-- swiper -->
        <swiper class="index-swiper" :current="activeIndex" @change="pageChange">
            <swiper-item class="swiper-item">
                <scroll-view scroll-y @scrolltolower="getBeforeArticle" :style="{'height': winHeight + 'px'}">
                    <article-list v-for="(item, index) in articles" :key="index" :json="item.stories" :date="item.formatDate"></article-list>
                    <div class="list-bottomLoad" v-if="articles.length > 0 && bottomLoading">
                        <div class="loading" v-if="bottomLoading === 'loading'">加载中...</div>
                        <div class="nothing" v-if="bottomLoading === 'nothing'">刷完了，休息一下吧</div>
                        <div class="error" v-if="bottomLoading === 'error'">出错了，刷新试试</div>
                    </div>
                </scroll-view>
            </swiper-item>
            <swiper-item class="swiper-item">
                <scroll-view scroll-y :style="{'height': winHeight + 'px'}">
                    <theme-list :json="themes"></theme-list>
                </scroll-view>
            </swiper-item>
        </swiper>
    </div>
</template>
<script>
import { getLatestArticle, getBeforeArticle, getThemes } from '@/api'
import { formatDate } from '@/utils'
export default {
    data() {
        return {
            winHeight: 0,
            tabsOption: ['最新消息', '主题日报'],
            activeIndex: 0,
            articles: [],
            bottomLoading: true,
            themes: []
        }
    },
    mounted() {
        this.getSystemInfo()
        this.getLatestArticle()
    },
    methods: {
        // 最新消息
        getLatestArticle() {
            getLatestArticle().then(res => {
                if (res) {
                    res.formatDate = formatDate(res.date)
                    this.articles = []
                    this.articles.push(res)
                }
            })
        },
        // 过往消息
        getBeforeArticle() {
            if (this.bottomLoading !== 'nothing' && this.bottomLoading !== 'error') {
                this.bottomLoading = 'loading'
                let lastDate = this.articles[this.articles.length - 1].date
                getBeforeArticle(lastDate).then(res => {
                    if (res) {
                        this.bottomLoading = true
                        res.formatDate = formatDate(res.date)
                        this.articles.push(res)
                    } else {
                        this.bottomLoading = 'nothing'
                    }
                }).catch(() => {
                    this.bottomLoading = 'error'
                })
            }
        },
        // 主题列表
        getThemes() {
            getThemes().then(res => {
                this.themes = res.others
            })
        },
        // 页面change
        pageChange(e) {
            this.activeIndex = e.mp.detail.current
            if (this.activeIndex === 0 && this.articles.length === 0) {
                this.getLatestArticle()
            } else if (this.activeIndex === 1 && this.themes.length === 0) {
                this.getThemes()
            }
        },
        // 获取系统信息 => 设置滚动页面高度
        getSystemInfo() {
            wx.getSystemInfo({
                success: (res) => {
                    this.winHeight = res.windowHeight - 50
                }
            })
        }
    },
    // 下拉刷新
    async onPullDownRefresh() {
        if (this.activeIndex === 0) {
            await this.getLatestArticle()
        } else {
            await this.getThemes()
        }
        wx.stopPullDownRefresh()
    }
}
</script>

<style lang="stylus">
$tabsHeight = 50px;
#index {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: #fff;
    .index-tabs {
        flex-center();
        box-sizing: border-box;
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        width: 100%;
        height: $tabsHeight;
        border-bottom: 1px solid #dcdfe6;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 0 3px 0 rgba(0, 0, 0, 0.04);
        backgroun-color: #fff;
        .tabs-item {
            position: relative;
            color: $mainText;
            font-size: $subTitleSize;
            padding: 8px 0;
            margin: 0 30px;
            &.active {
                color: $appColor;
                &:after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    height: 2px;
                    border-radius: 5px;
                    background-color: $appColor;
                }
            }
        }
    }
    .index-swiper {
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding-top: $tabsHeight;
        .swiper-item {
            overflow: auto;
        }
    }
}
</style>



